
// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);

// Variables
@import "variables";

.delete {
  position: absolute;
  padding-right: 1em;
  right: 0;
  cursor: pointer;
}
.actions {
   display: inline-block;
  border-left: 1px solid #ddd;
  padding-left: 15px;
  margin-left: 10px;
 }
.actions + .actions {
  border-left: 1px solid #ddd;
  padding-left: 15px;
  margin-left: 10px;
}
.body{
  display: inline-block;
  padding-left: 10px;
  margin-left: 10px;
  padding-right: 20px;
  border-left: 1px solid #ddd;
}
.priority {
  display: inline-block;
  min-width: 20px;
  text-align: right;
}

.tasks-done {
  .actions {

  }
  .body{
    text-decoration: line-through;
  }
  .priority {

  }
}